@import "_BV_variables";

.content {
	background-color: $bright;

	padding: 20px 60px;

	// Bootstrap Customizations
	.row {
		justify-content: flex-start;

		margin-left: 0px; }

	.col {
		flex-grow: 0;

		margin-bottom: 40px; } }

.book-title {
	margin-top: 5px;

	span {
		font-size: 0.9em;
		opacity: 0.7; } }

.book-cover {
	width: 200px; }

.book-rating {
	color: $goodreads-logo-color; }

.stars {
	float: left;
	letter-spacing: -0.07rem; }

.ratings-count {
	float: right; }
